            this.$parent.showHello = false;
        }
    }
};
</script>

<style lang ='scss' scoped>
@media screen and (max-width: 376px) {
    img {
        width: 300px;
    }
}
@keyframes fade {
    0% {
        opacity: 0;
    }
    100% {
        opacity: 1;
    }
}

.content {
    width: 100%;
    height: 100%;
    box-sizing: border-box;
    color: rgb(254, 255, 0);
    padding-top: 10%;
    background-color: #000;
    text-align: center;
}
.logo {
    animation: fade 5s ease;
}
.logo:hover {
    animation: normal;
}
h1 {
    width: 360px;
	margin: 22px auto;
	font-size: 36px;
    @include lgStr($strColor);
    animation: strAnm 3s linear infinite;
}

@keyframes bounce {
    25% {
        transform: translateX(0px);
    }
    50% {
        transform: translateX(-15px);
    }
    70% {
        transform: translateX(0px);
    }
    95% {
        transform: translateX(-5px);
    }
}

.btn {
    width: 220px;
    padding: 5px 10px;
    position: relative;
    overflow: hidden;
    margin: 0 auto;
    box-sizing: border-box;

    &:hover {
        cursor: pointer;
        .wave {
            top: -120px;
        }
    }

    a {
        position: relative;
        z-index: 1;
        color: aquamarine;
        font-size: 15px;
        letter-spacing: 8px;
    }

    .wave {
        width: 200px;
        height: 200px;
        background-image: linear-gradient(
            45deg,
            blue,
